import Vue from 'vue'
import Router from 'vue-router'

import Home from '../components/Home'
import Video from '../components/Video'
import Page from "../components/Page";

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/video/:tid?',   //需要传递标签id，可传可不传
      name: 'Video',
      component: Video
    },
    {
      path: '/page/:lessonId',  //需要传递lesson id 必须传
      name: 'Page',
      component: Page
    }
  ]
})



